import React, { Fragment, useEffect, useState } from "react";
import Media from "components/Media/Media";
import Linkify from "react-linkify";
import "../../assets/css/react-tiny-link-style.css";
import { useSelector } from "react-redux";
import ReactPlayer from 'react-player'
import LinkPreview from "./LinkPreview";

const ChatMessage = ({ avatar, title, message, id, event }) => {

    const [pressTime, setPressTime] = useState(0);
    const [intervalObject, setIntervalObject] = useState(undefined);
    const { messagefontSize } = useSelector(state => state.login);

    useEffect(() => {
        if (event) {
            event();
        }
    }, [event])

    useEffect(() => {
        if (pressTime >= 2) {
            console.log("long press");
            if (event) {
                event(id, intervalObject);
            } else {
                console.log("event not exist");
            }
        }
    }, [pressTime])

    const mouseDown = () => {
        console.log("on Mouse Down");
        let _pressTime = 0
        const t = setInterval(() => {
            _pressTime = _pressTime + 1;
            console.log(_pressTime);
            setPressTime(_pressTime);
        }, 1000);
        setIntervalObject(t);
        return false;
    }

    const mouseUp = () => {
        console.log("on Mouse Up");
        clearInterval(intervalObject);
        setPressTime(0);
        return false;
    }

    const chatFontSize = {
        fontSize: messagefontSize ? messagefontSize : "0.7rem",
        wordBreak: "break-word",
    }



    return (
        <Media
            avatar={avatar}
            title={
                <span style={chatFontSize}>
                    {title}
                </span>
            }
            body={
                <span>
                    <p style={chatFontSize} onMouseDown={mouseDown} onMouseUp={mouseUp}>
                        <Linkify componentDecorator={componentDecorator}>
                            {message}
                        </Linkify>
                    </p>
                </span>
            }
        />
    )
}

const componentDecorator = (href, text, key) => {

    const isYoutube = (href) => {
        let result = false;
        if (href.indexOf('youtu.be') !== -1) {
            result = true;
        }

        if (href.indexOf('https://www.youtube.com/') !== -1) {
            result = true;
        }

        return result;
    }

    const renderLink = () => {

        const chatFixedFontSize = {
            fontSize: "0.7rem",
            wordBreak: "break-word",
        }

        if (href && href !== "") {
            const check = href.indexOf('firebasestorage');
            if (check !== -1) {
                return (<></>)
            } else {
                return (
                    <a href={href} key={key} target="_blank" style={chatFixedFontSize} >
                        {text}
                    </a>
                )
            }
        } else {
            return (<></>)
        }
    }

    const renderLinkContent = () => {
        if (href && href !== "") {
            const check = href.indexOf('firebasestorage');
            if (check !== -1) {
                return (
                    <LinkPreview url={href} />
                )
            } else {
                if (isYoutube(href)) {
                    return (
                        <ReactPlayer
                            url={href}
                            loop={true}
                            width='100%'
                            height='100%'
                            style={{ border: "0.7rem solid white", borderRadius: "6px" }}
                        />
                    )
                } else {
                    return (
                        <>
                            <LinkPreview url={href} />
                        </>
                    )
                }
            }
        } else {
            return (<></>)
        }
    }



    return (
        <Fragment>
            {renderLink()}
            {renderLinkContent()}
        </Fragment>
    )
};

export default ChatMessage;