import React, { Fragment, useState, useRef } from "react";
import { imageRef } from "firebaseUtil/firebase";
import Media from "components/Media/Media";
import Button from 'components/CustomButtons/Button.js';
import CustomInput from "components/CustomInput/CustomInput";
import Reply from "@material-ui/icons/Reply";
import { Icon, makeStyles, Input } from "@material-ui/core";
import sectionCommentsStyle from "assets/jss/material-kit-pro-react/views/blogPostSections/sectionCommentsStyle.js";
import imageCompression from 'browser-image-compression';
import { storage } from "firebaseUtil/firebase";
import { getDownloadURL, ref as StorageRef, uploadBytes } from "firebase/storage";

const useStyles = makeStyles(sectionCommentsStyle);

const ChatInput = ({
    avatar, value, onChange, onReply,
    onUpdate, enableUpdate, onNotify, enableNotify,
    enableImageUpload, setReplyImageUrl
}) => {
    const classes = useStyles();

    const inputEl = useRef(null);
    const [imageAsFile, setImageAsFile] = useState(undefined);
    const [imageInput, setImageInput] = useState("");

    const onImageClick = e => {
        e.preventDefault();
        inputEl.current.click();
    }

    const onImageChange = async (e) => {
        const file = e.target.files[0];
        if (file) {
            const fileName = file.name;
            let compressedFile;
            let fileSize = file.size / 1024 / 1024;

            try {
                // console.log('originalFile instanceof Blob', imageFile instanceof Blob); // true
                console.log(`originalFile size ${file.size / 1024 / 1024} MB`);

                const options = {
                    maxSizeMB: 2,
                    maxWidthOrHeight: 1024,
                    useWebWorker: true
                }

                if (fileSize > 1.2) {
                    compressedFile = await imageCompression(file, options);
                } else {
                    compressedFile = file;
                }

                // console.log('compressedFile instanceof Blob', compressedFile instanceof Blob); // true
                console.log(`compressedFile size ${compressedFile.size / 1024 / 1024} MB`); // smaller than maxSizeMB

            } catch (error) {
                console.log(error.message);
            }

            if (compressedFile) {
                setImageAsFile(imageFile => (compressedFile));
            } else {
                setImageAsFile(file);
            }

            setImageInput(`${fileName}`);
        }
    }

    const onImageUpload = e => {

        setImageInput("Image upload...");

        const storageRef = imageRef(storage, `/images/${imageAsFile.name}`);

        uploadBytes(storageRef, imageAsFile)
            .then((snapshot) => {
                console.log(snapshot);
                // getDownloadURL(StorageRef(storage, `/images/${imageAsFile.name}`))
                getDownloadURL(StorageRef(storageRef))
                    .then(fireBaseUrl => {
                        setReplyImageUrl(fireBaseUrl);
                        console.log(fireBaseUrl);
                        setImageAsFile(undefined);
                    }).catch((error) => {
                        console.log(error);
                        alert(error);
                        setImageAsFile(undefined);
                    });
            })
            .catch((error) => {
                console.log(error);
                alert(error);
                setImageAsFile(undefined);
            });
    }

    return (
        <Fragment>
            <Media
                avatar={avatar}
                body={
                    imageAsFile ? (
                        <CustomInput
                            id="reply"
                            formControlProps={{
                                fullWidth: true
                            }}
                            inputProps={{
                                multiline: true,
                                rows: 1,
                                value: imageInput,
                                disabled: true,
                            }}
                        />
                    ) : (
                        <CustomInput
                            id="reply"
                            formControlProps={{
                                fullWidth: true
                            }}
                            inputProps={{
                                multiline: true,
                                rows: 4,
                                placeholder: " Write some nice stuff or nothing...",
                                onChange: onChange,
                                value: value,
                            }}
                        />
                    )
                }
                footer={
                    <div>
                        <Button
                            color="github"
                            className={classes.footerButtons}
                            onClick={imageAsFile ? onImageUpload : onReply}
                        >
                            <Reply /> Reply
                        </Button>
                        {enableUpdate ? (
                            <Fragment>
                                <div style={{ float: "right" }}>
                                    <div style={{ width: "5px", height: "41px" }} ></div>
                                </div>
                                <Button
                                    className={classes.footerButtons}
                                    justIcon round color="github"
                                    onClick={onUpdate}>
                                    <Icon>update</Icon>
                                </Button>
                            </Fragment>
                        ) : <></>}
                        {enableImageUpload ? (
                            <Fragment>
                                <div style={{ float: "right" }}>
                                    <div style={{ width: "5px", height: "41px" }} ></div>
                                </div>
                                <input type="file" style={{ display: "none" }} ref={inputEl} onChange={onImageChange} />
                                <Button
                                    className={classes.footerButtons}
                                    justIcon round color="github"
                                    onClick={onImageClick}>
                                    <Icon>insert_photo</Icon>
                                </Button>
                            </Fragment>
                        ) : <></>}
                        {enableNotify ? (
                            <Fragment>
                                <div style={{ float: "right" }}>
                                    <div style={{ width: "5px", height: "41px" }} ></div>
                                </div>
                                <Button
                                    className={classes.footerButtons}
                                    justIcon round color="github"
                                    onClick={onNotify}>
                                    <Icon>ring_volume</Icon>
                                </Button>
                            </Fragment>
                        ) : <></>}


                    </div>
                }
            />
        </Fragment >
    )
}

export default ChatInput;