import React, { Fragment, useState, useRef, useEffect } from "react";
import api from "axios";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Table from "components/Table/Table.js";
import Button from "components/CustomButtons/Button.js";
import Danger from "components/Typography/Danger.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import styles from "assets/jss/material-kit-pro-react/views/dashboardStyle";
import cardImage from "assets/img/examples/card-blog2.jpg";
import { makeStyles, Tooltip } from "@material-ui/core";
import { ArtTrack, Edit, Place, Refresh } from "@material-ui/icons";
import { useSelector } from "react-redux";


const LinkPreview = ({ url }) => {
    const useStyles = makeStyles(styles);
    const classes = useStyles();
    const { messagefontSize } = useSelector(state => state.login);
    const [info, setInfo] = useState(undefined);
    const [noCacheImageUrl, setNoCacheImageUrl] = useState("");

    const chatFixedFontSize = {
        fontSize: "0.7rem",
        wordBreak: "break-word",
    }

    useEffect(() => {
        const gcp_link_preview_api_url = "https://klk.company";
        const parameters = { url: url };

        api.post(gcp_link_preview_api_url, parameters)
            .then((response) => {
                if (response && response.data) {

                    const { data } = response;

                    const isFromDatabase = url.indexOf("firebasestorage.googleapis.com/") !== -1;
                    const isImage = url.indexOf("jpg") !== -1 || url.indexOf("jpeg") !== -1 || url.indexOf("png") !== -1 || url.indexOf("bmp") !== -1;

                    // url 
                    if (isFromDatabase && isImage) {

                        const _data = {
                            image: url,
                            title: "Lorem ipsum dolor sit amet.",
                            description: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus feugiat in turpis posuere malesuada. Donec non dolor eu lacus volutpat."
                        };

                        setInfo(_data);
                        return;
                    }

                    if (hasValue(data.image) || hasValue(data.title) || hasValue(data.description)) {
                        setInfo(data);
                    }
                }
            })
            .catch((error) => {
                console.log(error);
            })
    }, [])


    const hasValue = (data) => {
        if (data === null || data === undefined || data === "") {
            return false;
        } else {
            return true;
        }
    }


    const onClick = (e, url) => {
        e.preventDefault();
        window.open(url);
    }

    const getRandomInt = (min, max) => {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min) + min); //The maximum is exclusive and the minimum is inclusive
    }

    useEffect(() => {
        if (info && info.image) {
            try {
                var url = new URL(info.image);
                var search_params = url.searchParams;
                search_params.set('dummy', getRandomInt(1, 65535));
                url.search = search_params.toString();

                if (url.hostname === "firebasestorage.googleapis.com") {
                    setNoCacheImageUrl(url.toString());
                } else {
                    setNoCacheImageUrl(info.image);
                }
            } catch (error) {
                setNoCacheImageUrl(info.image);
            }
        }
    }, [info])

    if (info) {
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Card product className={classes.cardHover}>
                        <CardHeader image className={classes.cardHeaderHover}>
                            <a href={url} onClick={(e) => { onClick(e, url) }}>
                                <img src={noCacheImageUrl} alt="..." />
                            </a>
                        </CardHeader>
                        <CardBody>
                            <div className={classes.cardHoverUnder}>
                            </div>
                            <h4 className={classes.cardProductTitle}>
                                <a href={url} style={chatFixedFontSize} onClick={(e) => { onClick(e, url) }} >
                                    {info.title}
                                </a>
                            </h4>
                            <p className={classes.cardProductDesciprion} style={chatFixedFontSize} onClick={(e) => { onClick(e, url) }}>
                                {info.description}
                            </p>
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer>
        )
    } else {
        return (<>
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Card product className={classes.cardHover}>
                        <CardHeader image className={classes.cardHeaderHover}>
                            <a href={url} onClick={(e) => { onClick(e, url) }}>
                                <img src={cardImage} alt="..." />
                            </a>
                        </CardHeader>
                        <CardBody>
                            <div className={classes.cardHoverUnder} style={chatFixedFontSize}>
                            </div>
                            <h4 className={classes.cardProductTitle} style={chatFixedFontSize}>
                                Lorem ipsum dolor sit amet.
                            </h4>
                            <p className={classes.cardProductDesciprion} style={chatFixedFontSize}>
                                Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus feugiat in turpis posuere malesuada. Donec non dolor eu lacus volutpat.
                            </p>
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer>
        </>)
    }
}

export default LinkPreview;